import { FETCH_GROUP, FETCH_STUDENTS, REMOVE_GROUP } from '../consts.js'


const initialState = []


export const getData = (state = initialState, {type, payload, id}) => {
    switch(type){
        case FETCH_GROUP:
            return payload
        case REMOVE_GROUP:
            return state.filter(i => i.id !== id)
        default:
            return state
    }
}



export const getStudents = (state = initialState, {type, payload}) => {
    switch(type){
        case FETCH_STUDENTS:
            return payload
        default:
            return state
    }
}
